        }
      }
    }
    .carousel {
      @include max_pc_lg {
        position: relative;
        margin-right: auto;
        margin-left: auto;
      }

      ul {
        li {
          @include max_pc_lg {
            margin-right: 20px;
            width: 700px;
            transform: translateZ(0);

            backface-visibility: hidden;
            will-change: transform;

            a {
              color: inherit;
              text-decoration: none;

              figure {
                @include max_pc_lg {
                  display: flex;
                  overflow: hidden;
                  align-items: center;
                  justify-content: center;
                  width: 700px;
                  height: 360px;
                  border: 1px solid transparent;
                  transform: translateZ(0);

                  backface-visibility: hidden;
                  contain: strict;
                  mask-image: url(/assets/images/top/mask.svg);
                  -webkit-mask-repeat: no-repeat;
                  mask-repeat: no-repeat;
                  mask-size: 100% 100%;
                }

                img {
                   @include max_pc_lg {
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 0;
                    display: block;
                    width: 100%;
                    height: 100%;
                    transition-timing-function: cubic-bezier(.165,.84,.44,1);
                    transition-duration: 2s;
                    transition-property: transform;

                    backface-visibility: hidden;
                    object-fit: cover;
                    object-position: center;
                    will-change: transform;
                   }
                }
              }
              .description { //スライダー下部のキャプション群
                @include max_pc_lg {
                  position: relative;
                  top: -30px;
                  left: -30px;
                }

                p {
                  @include max_pc_lg {
                    display: inline-flex;
                    align-items: flex-start;
                    flex-direction: column;
                    margin-bottom: 20px;
                    letter-spacing: .08em;
                    font-size: 34px;
                  } 
                  span::before {
                    @include max_pc_lg {
                      position: absolute;
                      top: 0;
                      left: 0;
                      z-index: -1;
                      display: block;
                      width: 100%;
                      height: 100%;
                      background-color: #fff;
                      content: "";
                      transition-timing-function: cubic-bezier(.77,0,.175,1);
                      transition-duration: .5s;
                      transform: scaleX(0);
                      transform-origin: left top;
                    }

                    &:first-of-type::before {
                      transition-delay: 0s;
                    }
                    &:first-of-type:nth-of-type(2):before {
                      transition-delay: .3s;
                    }
                    &:nth-of-type(2):nth-of-type(3):before {
                      transition-delay: .6s;
                    }
                    &:nth-of-type(3):nth-of-type(4):before {
                      transition-delay: .9s;
                    }
                    &:nth-of-type(4):span {
                      transition-delay: 1.2s;
                    }
                  }
                  & > span {
                    @include max_pc_lg {
                      position: relative;
                      z-index: 1;
                      margin-top: 8px;
                      padding: 5px 10px;
                    }
                    &:first-of-type span {
                      transition-delay: .3s;
                    }
                    &:nth-of-type(2) span {
                      transition-delay: .6s;
                    }
                    span {
                      display: block;
                      overflow: hidden;
                      width: 0;
                      white-space: nowrap;
                      transition-timing-function: cubic-bezier(.77,0,.175,1);
                      transition-duration: .25s;
                      transition-property: width;
                    }
                  }
                }
              }
              .position { //所属部署のキャプション
                @include max_pc_lg {
                  display: flex;
                  align-items: flex-start;
                  min-height: 60px;
                  letter-spacing: .08em;
                  font-size: 16px;
                  line-height: 1.75;
                }

                span.dir span, span.name span{
                    display: block;
                    overflow: hidden;
                    width: 0;
                    white-space: nowrap;
                    transition-timing-function: cubic-bezier(.77,0,.175,1);
                    transition-duration: .25s;
                    transition-property: width;
                  }
                span.dir {
                  position: relative;
                  z-index: 1;
                  margin-right: 20px;
                  padding: 2px 8px;
                  color: #fff;
                  letter-spacing: .08em;

                  &::before {
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: -1;
                    display: block;
                    width: 100%;
                    height: 100%;
                    background-color: #00afec;
                    content: "";
                    transition-timing-function: cubic-bezier(.77,0,.175,1);
                    transition-duration: .5s;
                    transform: scaleX(0);
                    transform-origin: left top;
                  }
                }

                span.name {
                  position: relative;
                  top: 2px;
                }
              }
            }
          }
        }
        //スライダーのactiveクラスが付与されている要素に対してのアニメーション
        .active:hover {
          cursor: pointer;
        } 
        .active:hover img {
          transform: scale(1.1);
        }
        .active .description p>span:before {
          transform: scaleX(1);
        } 
        .active .description p>span span {
          width: 100%;
        } 
        .active .description .position span.dir span, .active .description .position span.name span {
          width: 100%;
          transition-delay: 1.5s;
        } 
        .active .description .position span.dir::before {
          transition-delay: 1.2s;
          transform: scaleX(1);
        }
      }
    }
  }
}