.js-hero-target {
  transition: all 1.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-property: opacity, transform;
}

.js-observer-target {
  opacity: 0;
  transition: all 1.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-property: opacity, transform;
  transform: translateY(15px);
  will-change: transition;
}
.js-observer-target.delay02 {
  transition-delay: 0.2s;
}
.js-observer-target.delay04 {
  transition-delay: 0.4s;
}
.js-observer-target.active {
  opacity: 1;
  transform: translateY(0);
}

@keyframes arrow-right-link {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  30% {
    opacity: 0;
    transform: translateX(20px);
  }
  70% {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes about-slide {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes layout-wiper-up_maskIn {
  0% {
    clip-path: inset(100% 0 0 0);
  }
  100% {
    clip-path: inset(0 0 0 0);
  }
}
@keyframes layout-wiper-up_maskIn-text {
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
